<template id="calendar">
    <div class="calendar_box">
        <div class="banner">
            <v-banner></v-banner>
        </div>


        <div class="rl_header">
            <div class="canlendarBgView">
                <div class="canlendarView">
                    <div class="canlendarTopView">
                        <div class="leftBgView">
                            <img class="old" src="../images/back.png" @click="oldMonth"/>
                        </div>
                        <div class="centerView">{{cur_year || "--"}} 年 {{cur_month || "--"}} 月</div>
                        <div class="rightBgView">
                            <img class="next" src="../images/back.png" @click="nextMonth"/>
                        </div>
                    </div>
                    <div class="weekBgView">
                        <div class="weekView" v-for="item in weeks_ch">{{item}}</div>
                    </div>
                    <div class="dateBgView">
                        <div class="dateEmptyView" v-for="item in empytGrids">{{item.index}}</div>
                        <div v-for="(item,index) in days" :key="index"
                             :class="[commonClass,_month == cur_month&&index == today-1? 'dateSelectView' : '']"
                             @click="showSwiper(item.index)">
                            <div class="datesView">{{item.index+1}}</div>
                            <img :src="item.smailUrl"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--<v-swiper_box v-if="swiperFlag" @click="hideSwiper()"></v-swiper_box>-->

        <div :class="[{show_box:isa,hidden_box:isb}]" @click="hideSwiper()">
            <div id="bg_back">
                <div class="swiper-container clickBox">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide"  v-for="mood in dayMoods">
                            <img :src="mood.bgUrl" alt="" />
                            <div class="clickBox_time">
                                <span>{{mood.dt}}</span><span>星期{{mood.weekCn}}</span><span>{{mood.time}}</span>
                                <div class="clickBox_bottom">{{mood.content}}</div>
                            </div>
                        </div>

                    </div>
                </div>

            </div>

        </div>

    </div>
</template>
<script type="text/javascript">
    import banner from "./banner.vue";

    var calendar = {
        template: '#calendar'
    }
    export default {
        components: {
            "v-banner": banner
        },
        data() {
            return {
                hasEmptyGrid: false,
                cur_year: '',
                cur_month: '',
                weeks_ch: ['日', '一', '二', '三', '四', '五', '六'],
                empytGrids: [],
                days: [],
                commonClass: 'dateView',
                _month: '',
                today: '',
                index: '',
                isa: false,
                isb: true,
                swiper_box: true,
                dayMoods:[],
                mySwiper:null
            }
        },

         mounted:function(){                                            //轮播配置
            let _this= this;
            _this.mySwiper = new Swiper('.swiper-container', {
            });
        },
        /*components:{
         "v-swiper_box":swiper_box
         },*/
        created: function () {
            this.setNowDate();
        },
        methods: {
            setNowDate: function () {
                var date = new Date();
                var cur_year = date.getFullYear();
                /**年份 */
                    //console.log(cur_year)
                var cur_month = date.getMonth() + 1;
                /**月 */
                    //console.log(cur_month)
                var todayIndex = date.getDay() - 1;
                var today = date.getDate();
                /**日 */
                //console.log(today)
                this.calculateEmptyGrids(cur_year, cur_month);
                /**调用计算空格子*/
                this.calculateDays(cur_year, cur_month);
                var date2 = new Date();
                var _month = date2.getMonth() + 1
                this.cur_year = cur_year,
                    this.cur_month = cur_month
                this._month = _month
                this.today = today
                //console.log(today)
                //console.log(cur_month)

            },
            getThisMonthDays(year, month) {
                return new Date(year, month, 0).getDate();
            },
            getFirstDayOfWeek(year, month) {
                return new Date(Date.UTC(year, month - 1, 1)).getDay();
            },
            calculateEmptyGrids(year, month) {
                /**计算空格子*/
                let _this = this;
                var firstDayOfWeek = this.getFirstDayOfWeek(year, month);

                var empytGrids = []
                if (firstDayOfWeek > 0) {
                    for (var i = 0; i < firstDayOfWeek; i++) {
                         empytGrids.push({index: i, date: "", smailUrl: ""});
                    }
                    _this.hasEmptyGrid = true;
                    _this.empytGrids = empytGrids;

                } else {
                    _this.hasEmptyGrid = false;
                    _this.empytGrids = []
                }


            },
            calculateDays(year, month) {
                let defaultImgUrl= web.IMG_PATH + "list_mood_0" + 0 + ".png";
                let _this = this;
                let days = [];
                let thisMonthDays = this.getThisMonthDays(year, month);
                let monthchange = month;
                if (month < 10) monthchange = "0" + monthchange;



                for (let i = 1; i <= thisMonthDays; i++) {
                    days.push({index: i-1, date: "", smailUrl: defaultImgUrl,moods:[] });
                }

                this.days = days;
                    days = [];
                _this.$http.get(web.API_PATH + 'mood/query/calendar/list/[userId]?date=' + year + '-' + monthchange + '-01').then(response => {
                    if (response.data.status === 1) {

                        if (thisMonthDays > 0) {
                            for (let i = 1; i <= thisMonthDays; i++) {
                                let dayChange = i;
                                if (i < 10) dayChange = "0" + i;
                                let dateStr = year + "-" + monthchange + "-" + dayChange;
                                let faceIndex = 0;
                                let moods =[];
                                for (let j = 0; j < response.data.data.length; j++) {
                                    if (dateStr === response.data.data[j].dt) {
                                        faceIndex = response.data.data[j].moodValue;
                                        moods.push(response.data.data[j]);
                                    }
                                }
                                let smailUrl = web.IMG_PATH + "list_mood_0" + faceIndex + ".png";
                                days.push({index: i-1, date: dateStr, smailUrl: smailUrl,moods:moods});
                            }


                        }

                        this.days = days

                    }else{
                        for (let i = 1; i <= thisMonthDays; i++) {
                            days.push({index: i-1, date: "", smailUrl: defaultImgUrl,moods:[]});
                        }
                    }
                }, response => {
                    for (let i = 1; i <= thisMonthDays; i++) {
                        days.push({index: i-1, date: "", smailUrl: defaultImgUrl,moods:[]});
                    }
                });

               //
            },
            oldMonth: function () {                             //上个月
                var cur_year = this.cur_year;
                var cur_month = this.cur_month;
                var newMonth = cur_month - 1;
                var newYear = cur_year;
                if (newMonth < 1) {
                    newYear = cur_year - 1;
                    newMonth = 12;
                }
                this.calculateDays(newYear, newMonth);
                this.calculateEmptyGrids(newYear, newMonth);
                this.cur_year = newYear,
                    this.cur_month = newMonth
            },
            nextMonth: function () {                             //下个月
                var cur_year = this.cur_year;
                var cur_month = this.cur_month;
                var newMonth = cur_month + 1;
                var newYear = cur_year;
                if (newMonth > 12) {
                    newYear = cur_year + 1;
                    newMonth = 1;
                }
                this.calculateDays(newYear, newMonth);
                this.calculateEmptyGrids(newYear, newMonth);
                this.cur_year = newYear,
                    this.cur_month = newMonth
            },
            showSwiper: function (index) {

                let _this=this;
                if(_this.days[index].moods.length>0){


                    //植入当天数据
                    _this.dayMoods=[];
                    _this.dayMoods =_this.days[index].moods;
                    for(let i =0 ;i<_this.dayMoods.length;i++){
                        _this.dayMoods[i].bgUrl= web.IMG_PATH +"bg_mood_0"+_this.dayMoods[i].moodValue+".png";
                        _this.dayMoods[i].dt = _this.dayMoods[i].dt.substring(5);
                        _this.dayMoods[i].dt=  _this.dayMoods[i].dt.replace("-","月");
                        _this.dayMoods[i].weekCn =  _this.weeks_ch[_this.dayMoods[i].weekix];
                    }


                    console.log(_this.dayMoods);
                    this.$nextTick(function(){

                        if(_this.mySwiper!==null){
                            _this.mySwiper.update()
                        }

                         _this. mySwiper.slideTo(_this.dayMoods.length-1, 0, false);//切换到第一个slide

                    });



                    //日期点击事件
                    this.isa = true;
                    this.isb = false


                }



            },
            hideSwiper: function () {                                 //轮播隐藏事件

                this.isa = false;
                this.isb = true
            }
        }

    }
</script>

<style>
    .show_box {
        visibility: inherit;
    }

    .hidden_box {
        visibility: hidden;
    }

    .calendar_box {
        height: 100%;
        background: #ffffff;
        padding-bottom: 100px;
    }

    .banner {
        width: 100%;
    }

    .banner img {
        display: block;
        width: 100%;
    }

    .rl_header {
        height: 60px;
        position: relative;

    }

    .old {
        left: 30px;
        height: 24px;
        width: 24px;
        position: absolute;
        top: 10px;
        display: block;
    }

    .next {
        right: 30px;
        transform: rotate(180deg);
        height: 24px;
        width: 24px;
        position: absolute;
        top: 10px;
        display: block;
    }

    .week_day text {
        flex: 1;
        text-align: center;
        color: #828080;
        font-size: 12px;
    }

    .canlendarBgView {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .canlendarView {
        color: #17b7b2;
        display: flex;
        flex-direction: column;
    }

    .canlendarTopView {
        height: 40px;
        font-size: 14px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }

    .leftBgView {
        text-align: right;
        height: 40px;
        display: flex;
        flex-direction: row-reverse;
    }

    .centerView {
        width: 50%;
        height: 40px;
        text-align: center;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        color: #666666;
    }

    .rightBgView {
        height: 40px;
        display: flex;
        flex-direction: row;
    }

    .weekBgView {
        height: 25px;
        line-height: 25px;
        opacity: 0.5;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    .weekView {
        flex-grow: 1;
        text-align: center;
        font-size: 14px;
        color: #333;
        border-bottom: 1px solid #eeeeee;

    }

    .dateBgView {
        height: 250px;
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .dateEmptyView {
        width: 14%;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        border-right: 1px solid #eeeeee;
        border-bottom: 1px solid #eeeeee
    }

    .dateView {
        width: 14%;
        color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-right: 1px solid #eeeeee;
        border-bottom: 1px solid #eeeeee
    }

    .dateView img {
        height: 30px;
        width: 30px;
        display: block;
        margin: 5px auto;
    }

    .datesView {

        height: 30px;
        color: #828080;
        font-size: 13px;
        display: flex;
        align-items: center;
        justify-content: center;

    }

    .dateSelectView {
        background: #fef8e5;
    }

    .clickBox_time {
        position: absolute;
        text-align: center;
        padding: 0 20px;
        bottom: 6px;
        font-size: 14px;
        color: #999999;
        height: 72px;
        overflow: hidden;
    }

    .clickBox_time span {
        margin: 0 5px;
    }

    .clickBox_bottom {
        margin-top: 6px;
    }

    .bg_box {

        position: fixed;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.5);
        height: 100%;
        width: 100%;
        z-index: 1;

    }

    .swiper_box {

        position: absolute;
        top: 25%;
        left: 50%;
        margin-left: -100px;
    }

    #bg_back {
        position: fixed;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.5);
        height: 100%;
        width: 100%;
        z-index: 100;
    }

    .clickBox {
        width: 100%;
        text-align: center;
        z-index: 100;
        border-radius: 10px;
        position: absolute;
        top: 30%;
        font-size: 18px;
        color: #666666;
        height: auto;
    }

    .clickBox img {
        width: 90%;
        height: auto;
    }

    .clickBox_time {
        position: absolute;
        bottom: 5%;
        width: 90%;
        height: 30%;
        left: 50%;
        margin-left: -50%;
    }

    .clickBox_time span {
        font-size: 12px;
        color: #999999;
        margin: 0 5px;
        line-height: 22px;;
    }

    .clickBox_bottom {
        font-size: 13px;
        color: #333333;
        line-height: 20px;
        padding: 0 36px;
        overflow: auto;
        height: 36px;
    }

    .swiper-slide {
        height: auto;
    }

</style>


